<template>
	<gui-page :customHeader="true">
		<view slot="gHeader">
			<view class="gui-flex gui-align-items-center gui-space-between gui-text">
				<view class="BackIcon"><text class="gui-grids-icon gui-icons"
						style="font-size: 40rpx; color:#333333; font-weight:600;" @click="goBack()">&#xe600;</text>
				</view>
				<text class="pageTitle"
					style="font: inter; font-size:36rpx; font-weight:600; color:#333333;">资讯详情</text>
				<view><text></text></view>
			</view>
		</view>
		</view>
		<view slot="gBody" class="gui-flex gui-column gui-border-t">
			<view class="gui-flex gui-justify-content-center gui-margin-top"
				style="font-size: 40rpx; margin-left:15rpx">
				<text class="firstArticle">{{newsInfo.title}}</text>
			</view>
			<view style="padding:90rpx 0rpx 40rpx 25rpx; color:#999999; font-size:24rpx">
				<text class="timeAndViews">{{newsInfo.synopsis}}</text>
			</view>
			<view style="margin-left: 25rpx;">
				<image :src="newsInfo.image" class="imageSize" mode=""></image>
			</view>
			<view class="imageArticleContainer" style="margin:25rpx 25rpx 0rpx 25rpx; font-size:30rpx color:#333333; ">
				<text class="imageArticleText"></text>
				<view class="desc" v-html="newsInfo.content"></view>
			</view>
			<!-- <view class="gui-margin-top" style="margin-left: 25rpx;" >
				<image src="../../static/InformationImage/Image2.png"  class="imageSize" mode="" @click="showArcticle()"></image>
			</view> -->

		</view>
	</gui-page>
</template>
<script>
	import request from "@/common/request.js"
	export default {
		data() {
			return {
				newsInfo: {},
				id: 0
			}

		},
		onLoad: function(data) {
			const fetchData = async () => {
				const response = await request.newsInfo({
					id: data.id
				})
				this.newsInfo = response.data.data;
			}
			fetchData()
		},
		methods: {
			goBack() {
				uni.navigateBack({
					delta: 2
				})
			},
			showArcticle: async function() {
				console.log("clicked")


			},

		}

	}
</script>

<style scoped>
	.imageSize {
		width: 700rpx;
		height: 350rpx;
	}
</style>
